.about {
  padding-bottom: calc($padding-section / 3);
  padding-top: $padding-section;

  background-color: $color-secondary-lighter;

  @include mq(mobile-only) {
    background-image: url(../images/about/milk-bg-mob.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0 594px;
    @include mq(retina) {
      background-image: url(../images/about/milk-bg-mob@2x.png);
    }
  }
  @include mq(tablet) {
    padding-bottom: calc($padding-section / 2);
  }
  @include mq(desktop) {
    background-image: url(../images/about/milk-bg-desk.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 236px;

    @include mq(retina) {
      background-image: url(../images/about/milk-bg-desk@2x.png);
    }
  }

  @media screen and (min-width: 1680px) {
    background-size: cover;
  }
}

.about__container {
  @include mq(mobile-only) {
    max-width: 480px;
  }
}

.about__top-text,
.about__title {
  text-align: center;
}

.about__title {
  margin-top: $gap;
}

.about__flex {
  margin-top: 32px;
  @include mq(tablet) {
    display: flex;
    margin-top: 40px;
  }
  @include mq(desktop) {
    margin-top: 60px;
  }
}

.about__picture {
  @include mq(tablet) {
    margin-right: 32px;
  }
  @include mq(desktop) {
    margin-right: 100px;
  }
}

.about__content {
  @include mq(mobile-only) {
    padding-right: 16%;
  }
  @include mq(tablet-only) {
    max-width: 334px;
  }
  @include mq(desktop) {
    max-width: 463px;
    padding-top: 90px;
  }
}

.about__text {
  @include font(12px, 162%, 500, 0.04em);
  color: $color-text-light;

  @include mq(mobile-only) {
    margin-top: 25px;
  }
  @include mq(tablet) {
    font-size: 14px;
    line-height: 176%;
  }
  @include mq(desktop) {
    line-height: 194%;
  }
}

.about__text--main {
  color: $color-text;

  @include mq(mobile-only) {
    margin-top: 40px;
  }
  @include mq(desktop) {
    font-size: 16px;
  }
}

.about__text--secondary {
  @include mq(tablet) {
    margin-top: 20px;
  }
  @include mq(desktop) {
    margin-top: 32px;
  }
}

.about__btn {
  margin-top: 20px;
  @include mq(tablet) {
    margin-top: 40px;
  }
}
